import * as React from 'react';
import Link from '@mui/material/Link';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Title from './Title';
import { PinState } from '../types';

function preventDefault(event: React.MouseEvent) {
  event.preventDefault();
}

export default function Orders({ rows }: {
  rows: PinState[]
}) {
  return (
    <React.Fragment>
      <Title>GPIO Pin State</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell width={100}>Pin</TableCell>
            <TableCell width={100}>Time</TableCell>
            <TableCell align="right">I/O</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(({ id, updatedAt, name, io }) => (
            <TableRow key={id}>
              <TableCell>{name}</TableCell>
              <TableCell >{`${updatedAt.getHours()}:${updatedAt.getMinutes()}`}</TableCell>
              <TableCell align="right">{io}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </React.Fragment>
  );
}
